import { Kit } from './../../entity/Kit';
import { KitServiceProvider } from './../../providers/kit-service/kit-service';
import { TabsPage } from './../tabs/tabs';
import { RepresentanteServiceProvider } from './../../providers/representante-service/representante-service';
import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ToastController } from 'ionic-angular';
import { Representante } from '../../entity/Representante';

@IonicPage()
@Component({
  selector: 'page-representante-edit',
  templateUrl: 'representante-edit.html',
  providers: [RepresentanteServiceProvider, KitServiceProvider]
})
export class RepresentanteEditPage {
  model: Representante;
  kits: any;
  kit_id: number;
  showKits: boolean;

  constructor(public navCtrl: NavController, public navParams: NavParams, 
              private toast: ToastController, private representanteProvider: RepresentanteServiceProvider,
              private kitProvider: KitServiceProvider ) {
    this.showKits = false;
    if (this.navParams.data.representante) {
      this.model = this.navParams.data.representante;
    } else {
      this.model = new Representante();
    }
  }

  changeShowKits(enable: boolean) {
    this.showKits = enable
  }

  save() {
    this.saveRepresentante().subscribe(
      () => {
        this.toast.create({ message: 'Representante salvo com sucesso.', position: 'botton', duration: 3000 }).present();
        this.navCtrl.setRoot(TabsPage)
        //this.navCtrl.pop();
      },
      (error) => {
        this.toast.create({ message: 'Erro ao salvar o representante. Erro: ' + error.error, position: 'botton', duration: 3000 }).present();
      });
  }

  private saveRepresentante() {
    if (this.model.id) {
      return this.representanteProvider.update(this.model);
    } else {
      return this.representanteProvider.insert(this.model);
    }
  }

  getAllKits() {    
    this.kitProvider.listAll().subscribe(
      response => this.kits = response
    );
  }

  addKit() {
    this.saveRepresentante().subscribe(
      (res: Representante) => {
        this.model = res;
      },
        (error) => {
          this.toast.create({ message: 'Erro ao salvar o representante. Erro: ' + error.error, position: 'botton', duration: 3000 }).present();
      },
      () => {
        if (this.kit_id)
          this.representanteProvider.addKit(this.model.id, this.kit_id).subscribe(res => console.log(res));
        this.toast.create({ message: 'Kit adicionado ao representante.', position: 'botton', duration: 3000 }).present();
      }
    );
  }

  ionViewDidEnter() {
    this.kits = [];
    this.getAllKits();
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad RepresentanteEditPage');
    //this.getAllKits();
  }
}



